import React, { useState, useEffect } from "react";
import "./navbar.scss";
import { Link, useLocation } from "react-router-dom";
import clsx from "clsx";
import logo from "../../media/logo/DAN-logo.png";
import Drawer from "./Drawer";

const Navbar = () => {
  const loc = useLocation();
  const [drawerState, setDrawerState] = useState(false);
  const [scrollOnTop, setScrollOnTop] = useState(true);

  const openMenu = () => {
    setDrawerState(true);
  };
  const closeMenu = () => {
    setDrawerState(false);
  };
  useEffect(() => {
    const handleScroll = () => {
      const currentScrollY = window.scrollY;
      if (currentScrollY > 65) {
        setScrollOnTop(false);
      } else {
        setScrollOnTop(true);
      }
    };
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);
  return (
    <>
      <header
        className={clsx("root-navbar", {
          "navbar-scroll": !scrollOnTop,
          "navbar-onTop": scrollOnTop,
        })}
      >
        <div className="hamburger-menu-container">
          <button onClick={openMenu}>
            <svg
              id="Layer_1"
              enableBackground="new 0 0 512 512"
              height="512"
              viewBox="0 0 512 512"
              width="512"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path d="m464.883 64.267h-417.766c-25.98 0-47.117 21.136-47.117 47.149 0 25.98 21.137 47.117 47.117 47.117h417.766c25.98 0 47.117-21.137 47.117-47.117 0-26.013-21.137-47.149-47.117-47.149z" />
              <path d="m464.883 208.867h-417.766c-25.98 0-47.117 21.136-47.117 47.149 0 25.98 21.137 47.117 47.117 47.117h417.766c25.98 0 47.117-21.137 47.117-47.117 0-26.013-21.137-47.149-47.117-47.149z" />
              <path d="m464.883 353.467h-417.766c-25.98 0-47.117 21.137-47.117 47.149 0 25.98 21.137 47.117 47.117 47.117h417.766c25.98 0 47.117-21.137 47.117-47.117 0-26.012-21.137-47.149-47.117-47.149z" />
            </svg>
          </button>
        </div>

        <div className="logo-container">
          <img src={logo} alt="company-logo" />
        </div>
        <nav>
          <ul>
            <li>
              <Link to="/" className={clsx({ active: loc.pathname === "/" })}>
                HOME
              </Link>
            </li>

            <li>
              <Link
                to="/about"
                className={clsx({ active: loc.pathname === "/about" })}
              >
                ABOUT
              </Link>
            </li>
            <li>
              <Link
                to="/projects"
                className={clsx({ active: loc.pathname === "/projects" })}
              >
                PROJECT
              </Link>
            </li>
            <li>
              <Link
                to="/services"
                className={clsx({ active: loc.pathname === "/services" })}
              >
                SERVICE
              </Link>
            </li>
            <li>
              <Link to="#">CONTACT</Link>
            </li>
          </ul>
        </nav>
        {drawerState ? <Drawer onClose={closeMenu} /> : null}
      </header>
    </>
  );
};

export default Navbar;
