import React, { useState, useEffect } from "react";
import "./home.scss";
import homePageImage from "../../media/home/homepage.png";
import homePageImage2 from "../../media/buildings/home2.jpg";
import homePageImage3 from "../../media/buildings/home3.jpg";
import homePageImage4 from "../../media/buildings/home4.jpg";
import homePageImage5 from "../../media/buildings/home5.jpg";
import homePageImage6 from "../../media/buildings/home6.jpg";

const images = [homePageImage4, homePageImage5, homePageImage6];
const Home = () => {
  const [imageIndex, setImageIndex] = useState(0);
  useEffect(() => {
    let timeout = setTimeout(() => {
      if (imageIndex === images.length - 1) {
        setImageIndex(0);
      } else {
        setImageIndex(imageIndex + 1);
      }
    }, 3500);

    return () => {
      clearTimeout(timeout);
    };
  }, [imageIndex]);

  return (
    <div className="root-home">
      <div
        className="home-intro"
        style={{
          backgroundImage: `url(${homePageImage})`,
        }}
      >
        <div>
          <p>
            Design Associated Nepal is a service oriented consulting firm
            offering Civil and Architecture related services
          </p>
        </div>
      </div>
      <div className="home-welcome">
        <div className="welcome-para">
          <p>
            <strong>Design Associates Nepal (DAN)</strong> , A noble startup
            company is a group of like minded individuals striving to deliver
            different services to our valued clients using the best of our
            abilities with the only motive of client satisfaction at top.
          </p>
          <p>
            DAN is not only focused on a particular service but is aligned to a
            multitude of services which helps our clients to get their
            multicategory jobs done from the same platform. Our qualified team
            members here at DAN have been providing valuable suggestions to
            client’s intent bringing the best possible designs that resonate to
            the client’s requirements.
          </p>
        </div>
        <div className="welcome-img">
          <img src={homePageImage2} alt="welcome to abc" />
        </div>
      </div>
      <div className="home-logos">
        <h1> LOGOS</h1>
      </div>
      <div className="home-specialization">
        <div className="specialization-items">
          <h2>Our Specialization</h2>
          <p>
            unity: a paragraph is a sentence or a group of sentences that
            supports one central, unified idea. Paragraphs add one idea at a
          </p>
          {[0, 1, 2, 3].map((item, index) => (
            <div key={index}>
              <img
                src="data:image/png;base64,"
                alt="placegolder "
              />
              <p>
                unity: a paragraph is a sentence or a group of sentences that
                supports one central, unified idea. Paragraphs add one idea at a
              </p>
            </div>
          ))}
        </div>
        <div className="specialization-img">
          <img src={homePageImage3} alt="specializaation" />
        </div>
      </div>
      <div className="home-logos">
        <h1>Project Count 275</h1>
      </div>
      <div className="home-projects">
        <div className="projects-para">
          <h2>Our Recent Projects</h2>
          <p>
            unity: a paragraph is a sentence or a group of sentences that
            supports one central, unified idea. Paragraphs add one idea at a
          </p>
          <button>Show all</button>
        </div>
        <div className="projects-img">
          <img src={images[imageIndex]} alt="pojects" />
        </div>
      </div>
    </div>
  );
};

export default Home;
