import React, { useEffect, useRef } from "react";
import "./navbar.scss";
import clsx from "clsx";
import { Link, useLocation } from "react-router-dom";

const Drawer = ({ onClose }) => {
  const loc = useLocation();
  const firstRender = useRef(true);
  const closeDrawer = (e) => {
    e.preventDefault();
    onClose();
  };
  useEffect(() => {
    if (firstRender.current) {
      firstRender.current = false;
    } else {
      onClose();
    }
    return () => {};
  }, [loc.pathname, onClose]);
  return (
    <aside className="root-drawer">
      <button onClick={closeDrawer}>
        <svg
          height="365.696pt"
          viewBox="0 0 365.696 365.696"
          width="365.696pt"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path d="m243.1875 182.859375 113.132812-113.132813c12.5-12.5 12.5-32.765624 0-45.246093l-15.082031-15.082031c-12.503906-12.503907-32.769531-12.503907-45.25 0l-113.128906 113.128906-113.132813-113.152344c-12.5-12.5-32.765624-12.5-45.246093 0l-15.105469 15.082031c-12.5 12.503907-12.5 32.769531 0 45.25l113.152344 113.152344-113.128906 113.128906c-12.503907 12.503907-12.503907 32.769531 0 45.25l15.082031 15.082031c12.5 12.5 32.765625 12.5 45.246093 0l113.132813-113.132812 113.128906 113.132812c12.503907 12.5 32.769531 12.5 45.25 0l15.082031-15.082031c12.5-12.503906 12.5-32.769531 0-45.25zm0 0" />
        </svg>
      </button>

      <div>
        <ul>
          <li>
            <Link to="/" className={clsx({ active: loc.pathname === "/" })}>
              HOME
            </Link>
          </li>

          <li>
            <Link
              to="/about"
              className={clsx({ active: loc.pathname === "/about" })}
            >
              ABOUT
            </Link>
          </li>
          <li>
            <Link
              to="/projects"
              className={clsx({ active: loc.pathname === "/projects" })}
            >
              PROJECT
            </Link>
          </li>
          <li>
            <Link
              to="/services"
              className={clsx({ active: loc.pathname === "/services" })}
            >
              SERVICE
            </Link>
          </li>
          <li>
            <Link to="#">CONTACT</Link>
          </li>
        </ul>
      </div>
    </aside>
  );
};

export default Drawer;
